.context-changer-content {
  padding: 8px 12px;
  font-size: 13px;
  color: #333;
  background-color: rgba(255, 152, 0, 0.08);
  border-radius: 4px;
  max-height: 100px;
  overflow-y: auto;

  &.empty {
    color: #999;
    font-style: italic;
  }

  .file-info {
    display: flex;
    align-items: center;
    gap: 6px;
    font-weight: 600;
    color: #ff9800;
    margin-bottom: 4px;

    mat-icon {
      font-size: 18px;
      width: 18px;
      height: 18px;
    }
  }

  .path-info {
    font-size: 11px;
    color: #666;
    padding-left: 24px;
  }
}
